@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@400;500&family=Open+Sans:wght@300;400&family=Poppins:wght@300;400;500;600&display=swap');

*{
  margin: 0;
  padding: 0;
  border: 0;
  outline: 0;
  box-sizing: border-box;
  list-style: none;
  text-decoration: none;
}
/* Colors To be Used thoughout project */
:root{

  --color-bg: #5d6d74;
  /* --color-bg: #1f1f38; */
  /* --color-bg-variant: #2c2c6c; */
  --color-bg-variant: #Be6134;
  /* --color-primary: #4bd5ff; */
  --color-primary: #b6a89b;
  --color-primary-variant: rgba(77,181,255,.4);
  /* --color-white: #fff; */
  --color-white: #f0e7db;
  --color-light: rgba(255,255,255,.6);
  
  --transition: all 400ms ease;
  --container-width-lg: 75%;
  --container-width--md: 86%;
  --container-width-ms:90;

}
html{
  scroll-behavior: smooth;
}

::-webkit-scrollbar{
  display: none;
}

body {

  font-family: 'Poppins', sans-serif;
  background: var(--color-bg);
  color: var(--color-white);
  line-height: 1.7;
}

/* General Container Styles */

.container{
  width: var(--container-width-lg);
  margin: 0 auto;
}

h1, h2, h3, h4, h5{
  font-weight: 500;
}

h1{
  font-size: 2.5rem;
}

section{
  margin-top: 8rem;
  height: 100vh;
}

section > h2, section > h5{
  text-align: center;
  color: var(--color-light);
}

section > h2{
  color: var(--color-primary);
  margin-bottom: 3rem;
}

.text-light{
  color: var(--color-light);
}

a{
  color: var(--color-primary);
  transition: var(--transition);
}
a:hover{
  color: var(--color-white);
}

.btn{
  width: max-content;
  display: inline-block;
  color: var(--color-primary);
  padding: .75rem 1.2rem;
  cursor: pointer;
  border: 1px solid var(--color-primary);
  border-radius: 1rem;
  transition: var(--transition);
}

.btn:hover{
  background: var(--color-white);
  color: var(--color-bg);
  border-color: transparent;
}

.btn-primary{
  background: var(--color-primary);
  color: var(--color-bg);
}

img{
  display: block;
  width: 100%;
  object-fit: cover;
}

/* media Queries  Tablets*/

@media screen and (max-width: 1024px){
  .container{
    width: var(--container-width--md);
  }
  section{
    margin-top: 6rem;
  }
}

/* media Queries  Phone*/
@media screen and (max-width: 600px){
  .container{
    width: var(--container-width-ms);
  }
  section > h2{
    margin-top: 2rem;
  }
};

